.NewsFullItem {
  z-index: 11;
  position: fixed;
  top: 0;
  right: -70px;
  width: 100%;
  max-width: 100%;
  height: 100%;
  background: #fff;
  padding: 3em;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity .3s ease, right .3s ease;
  -o-transition: opacity .3s ease, right .3s ease;
  transition: opacity .3s ease, right .3s ease;
  overflow-x: hidden;
  overflow-y: auto; }
  @media screen and (min-width: 769px) {
    .NewsFullItem {
      max-width: 50vw; } }
  .NewsFullItem.is-active {
    opacity: 1;
    visibility: visible;
    right: 0; }
  .NewsFullItem .NewsItem--full-info {
    display: block; }
  .NewsFullItem .NewsItem-pic {
    display: block;
    margin: 0 auto; }
    @media screen and (min-width: 769px) {
      .NewsFullItem .NewsItem-pic {
        width: 70%; } }
    @media screen and (min-width: 1025px) {
      .NewsFullItem .NewsItem-pic {
        width: 60%; } }
    @media screen and (min-width: 1367px) {
      .NewsFullItem .NewsItem-pic {
        width: 50%; } }
  .NewsFullItem .NewsItem-title {
    min-height: 100%;
    margin-top: 40px; }
  .NewsFullItem .NewsItem-description {
    min-height: 100%;
    font-size: 1.3em; }



