input {
  padding: 20px;
  border: none;
  font-size: 1.14em;
  font-family: inherit;
  font-weight: 300;
  background: rgba(255, 255, 255, 0.35);
  -webkit-transition: background-color .4s ease;
  -o-transition: background-color .4s ease;
  transition: background-color .4s ease; }
  input:hover {
    background: rgba(255, 255, 255, 0.65); }
  input:focus {
    outline: none;
    background: #fff; }



